
body{
  padding: 0;
  margin: 0;
  background-color: rgb(31, 89, 160);
}
#logoContainer{
  width: 100%;
}
#logo{
  height: 60vh;
  display: block;
  margin: 15% auto auto auto;
}

#Main[data-v-4b8e00f7]{
  text-align: center;
}
h1[data-v-4b8e00f7]{
  color: white;
}

#Start{
	display: grid;
	grid-template-columns: 50px calc(100% - 50px);
}
.container{
	width: 500px;
	height: 400px;
	overflow: hidden;
	position: relative;
	margin:50px auto;
}
.barcontainer{
	background-color: white;
	position: relative;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	top: 50%;
	width: 50px;
	height: 100vh;
}
.bar{
	background-color: #f77d24;
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 80%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
 	-webkit-animation: grow 1.5s ease-out forwards;
 	        animation: grow 1.5s ease-out forwards;
	-webkit-transform-origin: bottom;
	        transform-origin: bottom;
}
@-webkit-keyframes grow{
from{
		-webkit-transform: scaleY(0);
		        transform: scaleY(0);
}
}
@keyframes grow{
from{
		-webkit-transform: scaleY(0);
		        transform: scaleY(0);
}
}
  
